export default{
	name:'Footer',
	data(){
		return{
			show:true,
			login:'/login',
			list:[
			        {name:"首页",path:"/footer/index"},
			        {name:"分类",path:"/footer/classify"},
			        {name:"购物车",path:this.show==false?"/footer/cart":'/login'},
			        {name:"我的",path:this.show==false?"/footer/user":'/login'}
			      ]
		}
	},
	beforeCreate(){
		// bus.$on("give",function(name){
		// 	console.log(name)
		// })

		var login=localStorage.getItem("login")
		var lo=JSON.parse(login)
		// console.log(lo)
		if(lo){
			this.show=false
			// console.log(this.show)
			return
		}
	}
}



